<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header"><h4>Employees</h4></li>
            <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
                <div class="chip">{{employee.dept}}</div>{{employee.employe_id}}:{{employee.name}}
                <router-link class="secondary-content" v-bind:to="{name:'ViewEmployee',params:{employee_id:employee.employe_id}}">
                        <i class="fa fa-eye"></i>
                </router-link>
            </li>
        </ul>
        <div class="fixed-action-btn">
           <router-link to="/new-employee" class="btn-floating btn-large red">
           <i class="fa fa-plus"></i>
           </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
   name:'dashboard',
   data(){
       return{
           employees:[]
       }
   },
   created(){
       db.collection('employees').orderBy('employe_id').get().then(
           querySnapshot =>{
               querySnapshot.forEach(doc =>{
                   const data={
                       'id':doc.id,
                       'employe_id':doc.data().employe_id,
                       'name':doc.data().name,
                       'dept':doc.data().dept,
                       'position':doc.data().position,
                   }
                   this.employees.push(data)
               })
           }
       )

   }
}
</script>



